.header {
  width: 100%;
  z-index: 50;
  position: fixed;
  top: 0;
  left: 0;
}

.header:before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: var(--black);
  z-index: 2;
  transition: background-color var(--transition-func) var(--transition-dur);
  position: absolute;
  top: 0;
  left: 0;
}

body.light-theme .header:before {
  transition: background-color var(--transition-func) var(--transition-dur);
  background-color: #fff;
}

.header__container {
  max-width: 1216px;
  margin: 0 auto;
  padding: 0 32px;
}

.header__body {
  height: 60px;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.logo__link {
  align-items: center;
  gap: 8px;
  display: flex;
}

.logo__link img {
  z-index: 4;
  position: relative;
}

.mobile__menu {
  display: none;
}

.header__list {
  gap: 40px;
  display: flex;
}

.switcher {
  z-index: 2;
  position: relative;
}

.theme-switcher {
  width: 60px;
  height: 24px;
  -webkit-appearance: none;
  background: var(--liner);
  transition: var(--transition-func) var(--transition-dur);
  border-radius: 20px;
  outline: none;
  box-shadow: inset 0 0 5px #000;
}

.theme-switcher:checked {
  background: var(--revers-liner);
}

.theme-switcher:before {
  content: "";
  width: 24px;
  height: 24px;
  background: var(--white);
  transition: var(--transition-func) var(--transition-dur);
  border-radius: 20px;
  position: absolute;
  top: 3px;
  left: 4px;
  transform: scale(1.1);
  box-shadow: 0 2px 5px rgba(0, 0, 0, .2);
}

.theme-switcher:checked:before {
  background: var(--black);
  left: 40px;
}

@media screen and (max-width: 767px) {
  .mobile__menu {
    z-index: 2;
    display: block;
  }

  .mobile__menu button {
    color: var(--gray);
    text-transform: uppercase;
    transition: color var(--transition-func) var(--transition-dur);
    font-family: Roboto;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 14px;
  }

  .mobile__menu button:focus {
    color: var(--orange);
    transition: color var(--transition-func) var(--transition-dur);
  }

  .logo__link img {
    width: 32px;
    height: 32px;
  }

  .logo__link span {
    display: none;
  }

  .header__body {
    height: 48px;
  }

  .header__menu {
    width: 65%;
    height: 100%;
    background-color: var(--black);
    z-index: 3;
    transition: var(--transition-func) var(--transition-dur);
    border-radius: 0 10px 10px 0;
    padding: 80px 0 0;
    position: fixed;
    top: 0;
    left: -500px;
  }

  body.light-theme .header__menu {
    background-color: var(--white);
  }

  .header__list {
    display: block;
  }

  .header__link {
    text-transform: uppercase;
    padding: 0 0 24px 30px;
    font-family: Roboto;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
  }

  body.light-theme .header__link {
    color: var(--black);
    font-weight: 600;
  }

  .header__link a:focus {
    color: var(--orange);
    transition: color var(--transition-func) var(--transition-dur);
  }

