footer {
  padding: 28px 0;
  display: flex;
  flex-direction: column;
  gap: 8px;
  align-items: center;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
  color: var(--light-gray);

  @media (min-width: 768px) {
    flex-direction: row;
    justify-content: center;
    font-size: 16px;
    line-height: 1.19;
  }

  @media (min-width: 1280px) {
    padding: 40px 0;
  }
}

.footer-heart-icon {
  margin: 0 10px;
  fill: var(--orange);
}
//temporary
.footer-heart-icon:hover {
  stroke: none;
}
//_______________
.goit-students {
  text-decoration: underline;
}
.footer-copyright {
  margin-right: 7px;
}
.footer-wrapper.first-string {
  display: flex;
  column-gap: 5px;
}

.footer-students {
  width: 280px;
  display: flex;
  flex-wrap: wrap;
  row-gap: 16px;
  margin: 0 auto;

  @media (min-width: 768px) {
    width: 704px;
    column-gap: 16px;
    row-gap: 28px;
  }

  @media (min-width: 1280px) {
    row-gap: 32px;
    width: 1217px;
  }
}

.footer-students .film-card__genre {
  max-width: none;
}
